import React from "react";
import "./iframe.css";

export default function Iframe(props) {
  return (
    <div>
      <iframe
        title={props.title}
        className={props.class}
        width={props.width}
        height={props.height}
        src={props.src}
        frameborder="0"
        allowFullScreen="true"
      ></iframe>
    </div>
  );
}
